.layout_ef .tips {
  color: #bbbbbb;
  font-size: 12px;
}
.layout_ef .cardtitle {
  font-size: 14px;
}
.layout_ef .one {
  font-size: 10px;
  border: 1px solid black;
  border-radius: 100%;
  padding: 0px 3px;
  width: 10px;
  height: 10px;
  line-height: 10px;
  text-align: center;
  display: inline;
  color: black;
}
.layout_ef .box {
  background-color: #F2F9FF;
  min-height: 40px;
  width: 100%;
  bottom: 30px;
  margin-bottom: 20px;
  margin-top: 15px;
  line-height: 40px;
  border-radius: 2px;
  border: 1px solid #A8CFFF;
}
.layout_ef .box span,
.layout_ef .box label {
  font-size: 12px !important;
  color: black !important;
  font-family: 600;
}
.layout_ef .box label,
.layout_ef .box abbr {
  margin-left: 20px;
}
.layout_ef .box abbr {
  font-weight: 100;
  color: #D9D9D9;
  position: relative;
  top: -2px;
}
.layout_ef .arrow {
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-left: 10px solid #D9D9D9;
  border-bottom: 6px solid transparent;
  margin-right: 20px;
}
.layout_ef .notice {
  color: #5582F3;
  font-size: 20px;
  margin-left: 20px;
}
.layout_ef .cardtitle::before {
  content: "";
  background-color: #5582F3;
  padding-right: 2px;
  margin-right: 10px;
  border-radius: 2px;
}
.layout_ef .card {
  padding: 8px 0px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.layout_ef .card .card_li {
  background-color: #F2F9FF;
  border-radius: 2px;
  border: 1px solid #A8CFFF;
  width: 239px;
  padding: 16px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  cursor: pointer;
  margin-right: 10px;
  margin-bottom: 10px;
  flex: 0 0 auto;
  flex-shrink: 0;
}
.layout_ef .card .card_li .card_li_left {
  width: 14px;
  height: 14px;
}
.layout_ef .card .card_li .card_li_right {
  padding-left: 8px;
  padding-right: 16px;
  width: -webkit-fill-available;
  max-width: 210px;
  flex-shrink: 0;
}
.layout_ef .card .card_li .card_li_right .card_li_right_header {
  font-family: MicrosoftYaHeiSemibold;
  font-size: 14px;
  color: #212121;
  letter-spacing: 0;
  font-weight: 600;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  word-wrap: break-word;
}
.layout_ef .card .card_li .card_li_right .card_li_right_content {
  white-space: pre-wrap;
  overflow-wrap: break-word;
  font-family: MicrosoftYaHei;
  font-size: 14px;
  color: #666666;
  letter-spacing: 0;
  font-weight: 400;
  margin-top: 18px;
  display: -webkit-box;
  text-overflow: ellipsis;
  overflow: hidden;
  word-wrap: break-word;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  height: 36px;
  line-height: 18px;
  width: 185px;
}
.layout_ef .card .card_li .card_li_right .card_li_right_footer {
  margin-top: 8px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  align-items: center;
  vertical-align: middle;
}
.layout_ef .card .card_li .card_li_right .card_li_right_footer .card_li_right_footer_text {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #276FF5;
  letter-spacing: 0;
  font-weight: 400;
}
.layout_ef .card .card_li .card_li_right .card_li_right_footer .card_li_right_footer_icon {
  color: #276FF5;
}
.layout_ef .card .card_li:hover {
  border: #276FF5 1px solid;
}
.layout_ef .card .card_empty {
  width: 100%;
  padding: 20px 0px;
}
